<script>
import axios from "axios";
import { Hooper, Slide, Navigation as HooperNavigation } from "hooper";
import Navbar from "./Navbar.vue";
import Footer from "./Footer.vue";
import AccountWindow from "./AccountWindow.vue";
import LastComments from "./LastComments.vue";
export default {
    name: "Artcl",
    components: {
        Navbar,
        Footer,
        AccountWindow,
        Hooper,
        Slide,
        HooperNavigation,
        LastComments
    },
    props: ["lastComments", "user"],
    data() {
        return {
            Id: this.$route.params.Pid,
            article: {},
            slid: []
        };
    },
    mounted() {
        axios
            .get(`http://bohmann.shop/api/articles/${this.Id}/`)
            .then(response => (this.article = response.data));
        axios
            .get(`http://bohmann.shop/api/articles/${this.Id}/`)
            .then(response => (this.slid = response.data.images));
    }
};
</script>

